import React, { useState } from "react";
import './PasswordUpdate.scss';
import logo from '../../assets/images/blackbox-ip-logo.png';
import * as Yup from "yup";
import { useFormik } from 'formik';
import { useHistory, useParams } from "react-router-dom";
import { save } from "../../shared/APIHandler";
import { API_URLS } from "../../shared/Constants";
import { setContext } from "../../shared/SessionHelper";
import { toast } from 'react-toastify';
// import BlockUi from 'react-block-ui';
// import 'react-block-ui/style.css';



function PasswordUpdate() {

  const [toggleBlock, setToggleBlock] = useState(false);
  const history = useHistory();
  let { id } = useParams();

  // Formik
  const formik = useFormik({
    initialValues: {
      oldPassword: "",
      newPassword: "",
      confirmPassword: ""
    },
    validationSchema: Yup.object().shape({
      oldPassword: Yup.string().required("Please enter old password."),
      newPassword: Yup.string().required("Please enter new password.").matches(
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/,
        "Must Contain 8 Characters, One Uppercase, One Lowercase, One Number and One Special Case Character"
      ),
      confirmPassword: Yup.string().required("Please enter confirm password.").test('passwords-match', 'Password must match with New Password.', function (value) {
        return this.parent.newPassword === value
      })
    }),
    onSubmit: (values) => {
      // Integrate update password API
      const params = {
        "username": id,
        "password": values.oldPassword,
        "newPassword": values.newPassword
      };
      setToggleBlock(true);
      // POST API execution
      save(API_URLS.login, params, (error, response) => {
        if (response && response.data) {
          console.log("response.data: ", response.data);
          setContext({token: response.data.accessToken, userName: id});
          setToggleBlock(false);
          history.push("/bbx/home");
        } else {
          toast.error("Invalid User Name or Password.");
          setToggleBlock(false);
          console.log("error: ", error);
        }
      });
    },
  });

  return (
    // <BlockUi tag="div" blocking={toggleBlock}>
    <div className="password-update-container">
        <h2>Update Password</h2>
        <div className="login-logo">
          <img className="logo" src={logo} alt="IDS logo" />
        </div>

        <form onSubmit={formik.handleSubmit}>
          <div className="form-horizontal">
            <div className="form-group mt-3">
              <label className="control-label">Old Password</label>
              <input
                name="oldPassword"
                type="password"
                className="form-control"
                onChange={(e) => {
                  formik.setFieldValue(
                    "oldPassword",
                    e.target.value,
                  );
                }}
                />
              {formik.errors.oldPassword && <div id="feedback">{formik.errors.oldPassword}</div>}
            </div>

          <div className="form-group mt-3">
            <label className="control-label">New Password</label>
            <input
              name="newPassword"
              type="password"
              className="form-control"
              onChange={(e) => {
                formik.setFieldValue(
                  "newPassword",
                  e.target.value,
                );
              }}
            />
            {formik.errors.newPassword && <div id="feedback">{formik.errors.newPassword}</div>}
          </div>

          <div className="form-group mt-3">
            <label className="control-label">Confirm Password</label>
            <input
              name="confirmPassword"
              type="password"
              className="form-control"
              onChange={(e) => {
                formik.setFieldValue(
                  "confirmPassword",
                  e.target.value,
                );
              }}
            />
            {formik.errors.confirmPassword && <div id="feedback">{formik.errors.confirmPassword}</div>}
          </div>

            <div className="form-group mt-4">
              <button
                type="submit"
                className="btn btn-primary col-12"
                disabled={!formik.isValid || toggleBlock}
                >
                Submit
              </button>
            </div>
          </div>
        </form>
      </div>
    // </BlockUi>
  );
}

export default PasswordUpdate;
