  const applicationByDisposition = () => {
    get(
      `${API_URLS.auApplicationDisposition}${selectedArtUnit.value}`,
      (error, response) => {
        if (response && response.data) {
          const { Allowed, Abandoned } = response.data;
          const categories = [];
          const allowedData = [];
          const abandonedData = [];

          Allowed.forEach((element) => {
            categories.push(element.year);
          });
          Abandoned.forEach((element) => {
            if (!categories.includes(element.year)) {
              categories.push(element.year);
            }
          });

          categories.forEach((element) => {
            const allowed = Allowed.find((obj) => obj.year === element);
            allowed
              ? allowedData.push(Number(allowed.value))
              : allowedData.push(0);

            const abandoned = Abandoned.find((obj) => obj.year === element);
            abandoned
              ? abandonedData.push(Number(abandoned.value))
              : abandonedData.push(0);
          });

          setAreaChartData({
            categories: categories,
            series: [
              {
                name: "Allowed",
                data: allowedData,
                color: "#7fc12c",
              },
              {
                name: "Abandoned",
                data: abandonedData,
                color: "#2c3e50",
              },
            ],
            ref: areaChartRef,
            id: "areaChart",
          });
          showChartLoading(areaChartRef, false);
        } else {
          showChartLoading(areaChartRef, false);
        }
      }
    );
  };

