    showChartLoading(semiCircleChartRef3, true);
    showChartLoading(semiCircleChartRef4, true);

    showChartLoading(barChart1, true);
    showChartLoading(barChart2, true);
    showChartLoading(columnChart1, true);
    showChartLoading(columnChart2, true);
    showChartLoading(pieChart1, true);
    showChartLoading(pieChart2, true);
    showChartLoading(pieChart3, true);
    setFrequentFirmsLoader(true);
    setTopExaminerLoader(true);
